.current {
  flex-direction: column;
  margin-top: 3em;
}

.current__name {
  color: var(--clr-primary);
}

.current__role {
  margin-top: 1.2em;
}

.current__desc {
  font-size: 1rem;
  max-width: 600px;
}

.current__desc,
.current__contact {
  margin-top: 2.4em;
}

.current .link--icon {
  margin-right: 0.8em;
}

.current .btn--outline {
  margin-right: 1em;
}

@media (max-width: 600px) {
  .app .current {
    align-items: flex-start;
    margin-top: 2em;
  }
}
